.btn {
  font-family: inherit;
  font-size: inherit;
  font-weight: 600;
  border-radius: 42px;
  padding: 14px 40px;
  background-color: var(--hover-btn-bg-color);
  color: var(--primary-text-color);
  border-color: transparent;
  cursor: pointer;

  &:hover,
  &:focus {
    background-color: var(--primary-text-color);
    color: var(--hover-btn-bg-color);
    border-color: transparent;
  }
  @media (min-width: 768px) {
    font-size: 16px;
    padding: 18px 44px;
  }
}

.delete {
  margin-left: 8px;
  border-radius: 50%;
  background-color: var(--hover-btn-bg-color);
  border-color: transparent;
  padding: 11px;
  cursor: pointer;

  &:hover,
  &:focus {
    background-color: var(--primary-text-color);
    color: var(--hover-btn-bg-color);
    border-color: transparent;
  }

  @media (min-width: 768px) {
    padding: 15px 16px;
  }
}
